import React, { Component } from 'react';
import axios from 'axios';
import QuickBase from 'quickbase';

class Test extends Component {
    constructor(props) {
        super(props);

        // this.quickbase = new QuickBase({
        //     realm: 'mcftech',
        //     userToken: 'b2pcbi_u55_d4gc8c7cdmtxyufqr655dwzfvsc'
        // });
    }

    componentWillMount() {
        var url = 'https://mcftech.quickbase.com/db/bkcdrryt2?a=API_DoQuery&includeRids=1&userToken=b2pcbi_u55_d4gc8c7cdmtxyufqr655dwzfvsc&appToken=chibjr8c32gdsfe4a6gjdm58tid';
        axios.post(url)
          .then(function (response) {
            console.log(response);
          })
          .catch(function (error) {
            console.log(error);
          });

        this.quickbase.api('API_DoQuery', {
            dbid: 'bkcdrryt2',
            includeRids: true
        }).then((results)=>{
            console.log(results);
        }).catch((err)=>{
            console.log(err);
        });
    }


    render() {
        return (
            <div>
                <h1>This is a test</h1>
            </div>
        );
    }
}


export default Test;



